/**
 * Name: BizAgi Desktop Widget Process Tree
 * 
 * @author Christian Collazos
 */


bizagi.workportal.widgets.processtree.extend("bizagi.workportal.widgets.processtree", {}, {
    init: function (workportalFacade, dataService, params) {
        var self = this;
        self.showTitle = (params.showTitle != undefined) ? params.showTitle:true;
        self.searchKeyResource=params.searchKeyResource || "workportal-widget-processTree-search-in";
        self.searchKeyResource=bizagi.localization.getResource(self.searchKeyResource);
        self.svgIconsMode=params.svgIconsMode || false;

        self.processesTreeNew = params.newDesign;
        // Call base
        self._super(workportalFacade, dataService, params);

        //Load templates
        self.loadTemplates({
            "processTree.links": bizagi.getTemplate("bizagi.workportal.desktop.widgets.processtree").concat("#ui-bizagi-workportal-widget-processTree-links"),
            "processTree.wrapper": bizagi.getTemplate("bizagi.workportal.desktop.widgets.processtree").concat("#ui-bizagi-workportal-widget-processTree-wrapper"),
            "processTree": bizagi.getTemplate("bizagi.workportal.desktop.widgets.processtree").concat("#ui-bizagi-workportal-widget-processTree"),
            "processTree.selected": bizagi.getTemplate("bizagi.workportal.desktop.widgets.processtree").concat("#ui-bizagi-workportal-widget-processTree-selected"),
            useNewEngine: false
        });
    },

    renderContent: function () {
        var self = this;
        var template = self.getTemplate("processTree.links");
        var content;

        content = self.content = $.tmpl(template, { showTitle: self.showTitle });

        // Override canvas if it has been defined
        if (self.params.canvas) {
            content = self.content = self.params.canvas.html(content);
        }

        return content;
    },

    postRender: function () {
        var self = this;

        //load templates
        self.loadtemplates();
        //load initial data
        self.setupData();
    },

    /*
    * Load template vars
    */
    loadtemplates: function () {
        var self = this;
        self.applicationTreeWrapperTmpl = self.getTemplate("processTree.wrapper");
        self.applicationTreeTmpl = self.getTemplate("processTree");
        self.applicationTreeSelectedTmpl = self.getTemplate("processTree.selected");
    },

    /*
    * Setup form data
    */
    setupData: function () {
        var self = this;
        self.treeRouteSelected = [];
        self.treeRouteText = "";

        $.when(self.setupInitialData()).done(function () {
            self.setupLinks();
        });
    },

    /*
    * Load form data
    */
    setupInitialData: function () {
        var self = this,
            content = self.getContent(),
            def = $.Deferred();

        self.treeRouteSelected = [];

        //append tree to wrapper
        $.when($.tmpl(self.applicationTreeWrapperTmpl, {}), $.tmpl(self.applicationTreeSelectedTmpl, { searchKeyResource: self.searchKeyResource })).done(function (treeWrapper, processSelected) {
            content.append(treeWrapper);
            content.append(processSelected);
            def.resolve();
        });
        return def.promise();
    },
    /*
    * Implement links behaviour
    */
    setupLinks: function () {
        var self = this,
            content = self.getContent();

        var params = { action: "Applications" };

        if(self.params.userId){
            params.userId = self.params.userId
        }
        self.showSelectedProcessLabelWhenAllApplicationsSelected(); //At the beginning 'all applications' option is selected
        $("#allAppsLink", content).click(function () {
            self.showSelectedProcessLabelWhenAllApplicationsSelected();
            self.selectAllApplications(content);
        });
        $("#allAppsLinkSelected", content).click(function () {
            self.showSelectedProcessLabelWhenAllApplicationsSelected();
            self.selectAllApplications(content);
        });

        $("#selectAppLink", content).click(function () {
             self.hideSelectedProcessLabelWhenAllApplicationsNotSelected();
             self.selectOneAplication(content, params);  
        });
        $("#selectAppLinkSelected", content).click(function () {
             self.hideSelectedProcessLabelWhenAllApplicationsNotSelected();
             self.selectOneAplication(content, params);  
        });
    },
    showSelectedProcessLabelWhenAllApplicationsSelected: function(){
        var self = this,
        content = self.getContent();
        if(self.processesTreeNew === true){
            $("#allAppsLinkSelected", content).data('allAppSelected', true); //To know when all applications radio button option has been selected
            $("#searchInLabel", content).parent().parent().parent().show(); //To show th label that indicates All applications are selected
        } 
    },
    hideSelectedProcessLabelWhenAllApplicationsNotSelected: function(){
        var self = this,
        content = self.getContent();
        if(self.processesTreeNew === true){
            $("#allAppsLinkSelected", content).data('allAppSelected', false);
            $("#searchInLabel", content).parent().parent().parent().hide(); //To hide th label that indicates All applications are selected
        }
    },
    changeRadioIcon: function(param, content){
        if(param == "all"){
            $("#allAppsLinkSelected", content).addClass("biz-wp-tree-selected-option");
            $("#selectAppLinkSelected", content).removeClass("biz-wp-tree-selected-option");
            $("#selectAppLinkSelected", content).addClass("biz-wp-tree-not-selected-option");
        }
        else if(param == "one"){
            $("#selectAppLinkSelected", content).addClass("biz-wp-tree-selected-option");
            $("#allAppsLinkSelected", content).removeClass("biz-wp-tree-selected-option");
            $("#allAppsLinkSelected", content).addClass("biz-wp-tree-not-selected-option");
        }
    },
    selectAllApplications: function(content){
        var self = this;
        self.changeRadioIcon("all", content);

        $("#applicationsTree", content).removeClass("hidden");
        $("#applicationsTree", content).css("display", "none");
        $("#searchInLabel", content).html(bizagi.localization.getResource("workportal-widget-processTree-all-applications"));
        self.treeRouteSelected = [];
    },
    selectOneAplication: function(content, params){
        var self = this;
        if($("#applicationsTree.hidden", content).length){
            var idSelected = self.getTreeRouteSelected()[0]
            self.changeRadioIcon("one", content);
            
            $("#applicationsTree", content).removeClass("hidden");
            var selector = $('a[data-id='+idSelected+']', content).closest("#applicationsTree > .case-search-app-ul > li.biz-wp-tree-node").siblings();
            
            selector.find("svg.bz-icon-arrow-down:not([style='display: none;'])").each(function() {
                self.expandOrCollapseArrows({
                    expand: false,
                    itemSelected: $(this)[0]
                });
            });
            selector.find(".biz-wp-tree-list").empty();
            return false;
        }
        if(self.params.byUser == true && (!self.params.userId || self.params.userId == ""))
        {
            $("#applicationsTree", content).html("");
            $("#applicationsTree", content).css("display", "inline-block");
            $("#applicationsTree", content).append("<strong>Debe Seleccionar un usuario</strong>");
        }else{
            $.when(self.dataService.getApplicationCategoriesList(params)).done(function (applicationList) {
                self.changeRadioIcon("one", content);
                var appTree = $.tmpl(
                    self.applicationTreeTmpl, 
                    { 
                        type: applicationList.type, 
                        items: applicationList.items, 
                        svg: self.svgIconsMode 
                    }
                );
                $("#applicationsTree", content).html("");
                $("#applicationsTree", content).css("display", "inline-block");
                $("#applicationsTree", content).append(appTree);

                //manage events
                self.setTreeEvents(null, content);

            }).fail(function (error) {
                bizagi.log(error);
            });
        }
    },
    /*
    * manage tree behaviour
    */
    setTreeEvents: function (idApp, content) {
        var self = this;

        //when select a process
        $(".biz-wp-tree-process", content).click(function () {

            var parentType = "";
            var selectedRoute = [];
            var selectedRouteText = "";
            var item = this;
            self.treeRouteSelected = [];
            self.treeRouteSelected.push($(this)[0].getAttribute("data-id"));

            //get the route (app / category / process) selected            
            while (parentType != "app") {
                item = $(item).closest(".biz-wp-tree-list").parent().find("label").first();
                parentType = item[0].getAttribute("data-name");
                selectedRoute.push(item.html());
                self.treeRouteSelected.push(item[0].getAttribute("data-id"));
            }

            for (var i = selectedRoute.length - 1; i >= 0; i--) {
                selectedRouteText += selectedRoute[i] + " > ";
            }

            selectedRouteText += $(this).html();
            self.treeRouteText = selectedRouteText;
            //resolve deferred to let parent do something when user select any process
            if (self.params.parentDef) {
                self.params.parentDef.resolve();
            }
            $(self.params.canvas).trigger("processSelected", 1);
            
            //update search in label with the selection
            
            $("#searchInLabel", self.params.canvas).html(selectedRouteText);
            if(self.processesTreeNew === true){
                $("#searchInLabel", self.params.canvas).parent().parent().parent().show(); //Show only when a process is selected
            }
            $("#applicationsTree", self.params.canvas).addClass("hidden");
        });

        //when select a category
        $(".biz-wp-tree-expand-icon", content).click(function () {
            var itemSelected = this;
            var idItem = Number(itemSelected.getAttribute("data-id"));
            var idParent = "";

            //when click a category remove the content to append the new content again and avoid repetition of data
            $(itemSelected).closest(".biz-wp-tree-node").find(".biz-wp-tree-list").html("");

            //when tree item is closed and can be expanded
            if ($("<b></b>").addClass($(itemSelected).attr("class")).hasClass("biz-wp-tree-expand-icon")) {
                
                self.expandOrCollapseArrows({ 
                        expand: true, 
                        itemSelected: itemSelected 
                    });

                if (idApp == null || itemSelected.getAttribute("name") === "app") {
                    idApp = idItem;
                } else {
                    idParent = idItem;
                }

                var params = {
                    action: "getCategories",
                    idApp: idApp,
                    idCategory: idParent,
                    filterStartEvent: false,
                    authorizationContext: self.params.authorizationContext
                };

                if(self.params.userId){
                    params.userId = self.params.userId
                }
                if(self.params.filterCaseSecurity){
                    params.filterCaseSecurity = self.params.filterCaseSecurity
                }

                $.when(self.dataService.getApplicationCategoriesList(params)).done(function (categoriesList) {
                    //load children and set events to them
                    var appTree = $.tmpl(
                        self.applicationTreeTmpl, 
                        { 
                            type: categoriesList.type, 
                            items: categoriesList.processes, 
                            svg: self.svgIconsMode 
                        }
                    );
                    $(itemSelected).closest(".biz-wp-tree-node").find(".biz-wp-tree-list").append(appTree);
                    self.setTreeEvents(idApp, $(itemSelected).closest(".biz-wp-tree-node").find(".biz-wp-tree-list"));

                }).fail(function (error) {
                    bizagi.log(error);
                });
            }
            //when tree item is open and can be closed
            else {
                self.expandOrCollapseArrows({ 
                    expand: false, 
                    itemSelected: itemSelected 
                });
            }
        });
    },

    /**
     * let to expand or collapse arrows dending of the svgIconsMode
     * @param {JSON} params expand and itemSelected
     */
    expandOrCollapseArrows: function(params) {
        var self = this;
        var right, down ="";
        if (params.expand) {
            right = $(params.itemSelected);
            right.attr("class", $("<b></b>").addClass(right.attr("class")).removeClass("biz-wp-tree-expand-icon").attr("class"));
            right.attr("class", $("<b></b>").addClass(right.attr("class")).addClass("biz-wp-tree-collapse-icon").attr("class"));
            if (self.svgIconsMode) {
                down = $(params.itemSelected).parent().find(".bz-icon-arrow-down");
                down.show();
                down.attr("class", $("<b></b>").addClass(down.attr("class")).removeClass("biz-wp-tree-expand-icon").attr("class"));
                down.attr("class", $("<b></b>").addClass(down.attr("class")).addClass("biz-wp-tree-collapse-icon").attr("class"));
                right.hide();
            }
        } else {
            right = self.svgIconsMode?$(params.itemSelected).parent().find(".bz-icon-arrow-right"):$(params.itemSelected);
            right.attr("class", $("<b></b>").addClass(right.attr("class")).removeClass("biz-wp-tree-collapse-icon").attr("class"));
            right.attr("class", $("<b></b>").addClass(right.attr("class")).addClass("biz-wp-tree-expand-icon").attr("class"));
            if (self.svgIconsMode) {
                right.show();
                down = $(params.itemSelected);
                down.hide();
                down.attr("class", $("<b></b>").addClass(down.attr("class")).removeClass("biz-wp-tree-collapse-icon").attr("class"));
                down.attr("class", $("<b></b>").addClass(down.attr("class")).addClass("biz-wp-tree-expand-icon").attr("class"));
            }
        }
    },

    /*
    * Returns the app, category and process selected
    */
    getTreeRouteSelected: function () {
        var self = this;
        return self.treeRouteSelected;
    },

    /*
    * Returns the app, category and process text selected
    */
    getTreeRouteText: function () {
        var self = this;
        return self.treeRouteText;
    },

    /**
     *  Set canvas of tree
     */
    setParamCanvas: function(canvas) {
        var self = this;
        self.params.canvas = canvas;
    }

});