(function ($) {
    $.widget("ui.selectmenu", {
        getter: "value",
        version: "1.8",
        eventPrefix: "selectmenu",
        options: {
            transferClasses: true,
            style: 'popup',
            width: null,
            menuWidth: null,
            handleWidth: 26,
            maxHeight: null,
            icons: null,
            format: null
        },

        _create: function () {
            var self = this, o = this.options;

            //quick array of button and menu id's
            this.ids = [this.element.attr('id') + '-' + 'button', this.element.attr('id') + '-' + 'menu'];

            //define safe mouseup for future toggling
            this._safemouseup = true;

            //create menu button wrapper
            this.newelement = $('<a class="' + this.widgetFullName + ' ui-widget ui-state-default ui-corner-all" id="' + this.ids[0] + '" role="button" href="#" aria-haspopup="true" aria-owns="' + this.ids[1] + '"></a>')
			.insertAfter(this.element);

            //transfer tabindex
            var tabindex = this.element.attr('tabindex');
            if (tabindex) { this.newelement.attr('tabindex', tabindex); }

            //save reference to select in data for ease in calling methods
            this.newelement.data('selectelement', this.element);

            //menu icon
            this.selectmenuIcon = $('<span class="' + this.widgetFullName + '-icon ui-icon"></span>')
			.prependTo(this.newelement)
			.addClass((o.style == "popup") ? 'ui-icon-triangle-2-n-s' : 'ui-icon-triangle-1-s');


            //make associated form label trigger focus
                
            $('label[for=' + this.element.attr('id') + ']')
			.attr('for', this.ids[0])
			.bind('click', function () {
			    try {
			        self.newelement[0].focus();
			    } catch (e) { }
			    return false;
			});

            //click toggle for menu visibility
            this.newelement
			.bind('mousedown', function (event) {
			    self._toggle(event);
			    //make sure a click won't open/close instantly
			    if (o.style == "popup") {
			        self._safemouseup = false;
			        setTimeout(function () { self._safemouseup = true; }, 300);
			    }
			    return false;
			})
			.bind('click', function () {
			    return false;
			})
			.keydown(function (event) {
			    var ret = true;
			      switch (event.keyCode) {
			        case $.ui.keyCode.ENTER:
			            ret = true;
			            break;
			        case $.ui.keyCode.SPACE:
			            ret = false;
			            self._toggle(event);
			            break;
			        case $.ui.keyCode.UP:
			        case $.ui.keyCode.LEFT:
			            ret = false;
			            // Next conditional prevents to close the combo
			            if (self.list.is('.ui-selectmenu-open'))  {
			                self._moveSelection(-1);
			                self.open();
			            } else self._moveSelection(-1);
			            break;
			        case $.ui.keyCode.DOWN:
			        case $.ui.keyCode.RIGHT:
			            ret = false;
			            // Next conditional prevents to close the combo
			            if (self.list.is('.ui-selectmenu-open'))  {
			                self._moveSelection(1);
			                self.open();
			            } else self._moveSelection(1);
			            break;
			        case $.ui.keyCode.TAB:
			            ret = true;
			            break;
			        default:
			            ret = false;
			            //self._typeAhead(event.keyCode, 'mouseup');
			            // Mouseup creates a problem on the first selectmenuOpen
			             self._typeAhead(event.keyCode, 'focus');
			            break;
			    }
			    return ret;
			})
			.bind('mouseover focus', function () {
			    $(this).addClass(self.widgetFullName + '-focus ui-state-hover');
			})
			.bind('mouseout blur', function () {
			    $(this).removeClass(self.widgetFullName + '-focus ui-state-hover');
			});

            //document click closes menu
            $(document)
			.mousedown(function (event) {
			    self.close(event);
			});

            //change event on original selectmenu
            this.element
			.click(function () { this._refreshValue(); })
            // newelement can be null under unclear circumstances in IE8 
			.focus(function () { if (this.newelement) { this.newelement[0].focus(); } });
            
            //create menu portion, append to body
            var cornerClass = (o.style == "dropdown") ? " ui-corner-bottom" : " ui-corner-all"
            this.list = $('<ul class="' + self.widgetFullName + '-menu ui-widget ui-widget-content' + cornerClass + '" aria-hidden="true" role="listbox" aria-labelledby="' + this.ids[0] + '" id="' + this.ids[1] + '"></ul>').appendTo('body');
            //serialize selectmenu element options	
            var selectOptionData = [];
            this.element
			.find('option')
			.each(function () {
			    selectOptionData.push({
			        value: $(this).attr('value'),
			        text: self._formatText(jQuery(this).text()),
			        selected: $(this).attr('selected'),
			        classes: $(this).attr('class'),
			        parentOptGroup: $(this).parent('optgroup').attr('label')
			    });
			});

            //active state class is only used in popup style
            var activeClass = (self.options.style == "popup") ? " ui-state-active" : "";

            //write li's
            for (var i = 0; i < selectOptionData.length; i++) {
                var thisLi = $('<li role="presentation"><a href="#" tabindex="-1" role="option" aria-selected="false">' + selectOptionData[i].text + '</a></li>')
				.data('index', i)
				.addClass(selectOptionData[i].classes)
				.data('optionClasses', selectOptionData[i].classes || '')
				.mouseup(function (event) {
				    if (self._safemouseup) {
				        var changed = $(this).data('index') != self._selectedIndex();
				        self.index($(this).data('index'));
				        self.select(event);
				        if (changed) { self.change(event); }
				        self.close(event, true);
				    }
				    return false;
				})
				.click(function () {
				    return false;
				})
				.bind('mouseover focus', function () {
				    self._selectedOptionLi().addClass(activeClass);
				    self._focusedOptionLi().removeClass(self.widgetFullName + '-item-focus ui-state-hover');
                    $(this).removeClass('ui-state-active').addClass(self.widgetFullName + '-item-focus ui-state-hover');
				})
				.bind('mouseout blur', function () {
				    if ($(this).is(self._selectedOptionLi())) { $(this).addClass(activeClass); }
				    if (!$.browser.msie) $(this).removeClass(self.widgetFullName + '-item-focus ui-state-hover');
				});

                //optgroup or not...
                if (selectOptionData[i].parentOptGroup) {
                    // whitespace in the optgroupname must be replaced, otherwise the li of existing optgroups are never found
                    var optGroupName = self.widgetFullName + '-group-' + selectOptionData[i].parentOptGroup.replace(/[^a-zA-Z0-9]/g, "");
                    if (this.list.find('li.' + optGroupName).size()) {
                        this.list.find('li.' + optGroupName + ':last ul').append(thisLi);
                    }
                    else {
                        $('<li role="presentation" class="' + self.widgetFullName + '-group ' + optGroupName + '"><span class="' + self.widgetFullName + '-group-label">' + selectOptionData[i].parentOptGroup + '</span><ul></ul></li>')
						.appendTo(this.list)
						.find('ul')
						.append(thisLi);
                    }
                }
                else {
                    thisLi.appendTo(this.list);
                }

                //this allows for using the scrollbar in an overflowed list
                this.list.bind('mousedown mouseup', function () { return false; });

                //append icon if option is specified
                if (o.icons) {
                    for (var j in o.icons) {
                        if (thisLi.is(o.icons[j].find)) {
                            thisLi
							.data('optionClasses', selectOptionData[i].classes + ' ' + self.widgetFullName + '-hasIcon')
							.addClass(self.widgetFullName + '-hasIcon');
                            var iconClass = o.icons[j].icon || "";

                            thisLi
							.find('a:eq(0)')
							.prepend('<span class="' + self.widgetFullName + '-item-icon ui-icon ' + iconClass + '"></span>');
                        }
                    }
                }
            }

            //add corners to top and bottom menu items
            this.list.find('li:last').addClass("ui-corner-bottom");
            if (o.style == 'popup') { this.list.find('li:first').addClass("ui-corner-top"); }

            //transfer classes to selectmenu and list
            if (o.transferClasses) {
                var transferClasses = this.element.attr('class') || '';
                this.newelement.add(this.list).addClass(transferClasses);
            }

            //original selectmenu width
            var clonedSelect = this.element.clone().appendTo("body");
            var selectWidth = clonedSelect.css({ position: "absolute", visibility: "hidden", display: "block" }).width();
            clonedSelect.remove();

            //set menu button width
            this.newelement.width((o.width) ? o.width : selectWidth);

            //set menu width to either menuWidth option value, width option value, or select width 
            if (o.style == 'dropdown') { this.list.width((o.menuWidth) ? o.menuWidth : ((o.width) ? o.width : selectWidth)); }
            else { this.list.width((o.menuWidth) ? o.menuWidth : ((o.width) ? o.width - o.handleWidth : selectWidth - o.handleWidth)); }

            //set max height from option 
            if (o.maxHeight && o.maxHeight < this.list.height()) { this.list.height(o.maxHeight); }

            //save reference to actionable li's (not group label li's)
            this._optionLis = this.list.find('li:not(.' + self.widgetFullName + '-group)');

            //transfer menu click to menu button
            this.list
			.keydown(function (event) {
			    var ret = true;
			    switch (event.keyCode) {
			        case $.ui.keyCode.UP:
			        case $.ui.keyCode.LEFT:
			            ret = false;
			            //self._moveFocus(-1);
			            self._moveSelection(-1);
			            self.open();
			            break;
			        case $.ui.keyCode.DOWN:
			        case $.ui.keyCode.RIGHT:
			            ret = false;
			            //self._moveFocus(1);
			            self._moveSelection(1);
			            self.open();
			            break;
			        case $.ui.keyCode.HOME:
			            ret = false;
			            self._moveFocus(':first');
			            break;
			        case $.ui.keyCode.PAGE_UP:
			            ret = false;
			            self._scrollPage('up');
			            break;
			        case $.ui.keyCode.PAGE_DOWN:
			            ret = false;
			            self._scrollPage('down');
			            break;
			        case $.ui.keyCode.END:
			            ret = false;
			            self._moveFocus(':last');
			            break;
			        case $.ui.keyCode.ENTER:
			        case $.ui.keyCode.SPACE:
			            ret = false;
			            self.close(event, true);
			            $(event.target).parents('li:eq(0)').trigger('mouseup');
			            break;
			        case $.ui.keyCode.TAB:
			            ret = true;
			            self.close(event, true);
			            break;
			        case $.ui.keyCode.ESCAPE:
			            ret = false;
			            self.close(event, true);
			            break;
			        default:
			            ret = false;
			            self._typeAhead(event.keyCode, 'focus');
			            break;
			    }
			    return ret;
			});

            //selectmenu style
            if (o.style == 'dropdown') {
                this.newelement
				.addClass(self.widgetFullName + "-dropdown");
                this.list
				.addClass(self.widgetFullName + "-menu-dropdown");
            }
            else {
                this.newelement
				.addClass(self.widgetFullName + "-popup");
                this.list
				.addClass(self.widgetFullName + "-menu-popup");
            }

            //append status span to button
            if (this._selectedIndex() && this._selectedIndex() != -1) {
                this.newelement.prepend('<span class="' + self.widgetFullName + '-status">' + selectOptionData[this._selectedIndex()].text + '</span>');
            } else {
                this.newelement.prepend('<span class="' + self.widgetFullName + '-status"></span>');
            }

            //hide original selectmenu element
            this.element.hide();

            //transfer disabled state
            if (this.element.attr('disabled') == true) { this.disable(); }

            //update value
            this.index(this._selectedIndex());

            // needed to make menu work when placed at the very bottom of a site
            setTimeout(function () { self._refreshPosition(); }, 200);

            // needed when window is resized
            $(window).resize(function () {
                self._refreshPosition();
            });
        },
        destroy: function () {
            this.element.removeData(this.widgetName)
			.removeClass(this.widgetFullName + '-disabled' + ' ' + this.namespace + '-state-disabled')
			.removeAttr('aria-disabled');

            //unbind click on label, reset its for attr
            var label = $('label[for=' + this.newelement.attr('id') + ']')
			             .attr('for', this.element.attr('id'));
			if (label) label.unbind('click');
            this.newelement.remove();
            this.list.remove();
            this.element.show();
        },
        _typeAhead: function (code, eventType) {
            var self = this;
            //define self._prevChar if needed
            if (!self._prevChar) { self._prevChar = ['', 0]; }
            var C = String.fromCharCode(code);
            c = C.toLowerCase();
            var focusFound = false;
            function focusOpt(elem, ind) {
                focusFound = true;
                $(elem).trigger(eventType);
                self._prevChar[1] = ind;
            };
            this.list.find('li a').each(function (i) {
                if (!focusFound) {
                    var thisText = $(this).text();
                    if (thisText.indexOf(C) == 0 || thisText.indexOf(c) == 0) {
                        if (self._prevChar[0] == C) {
                            if (self._prevChar[1] < i) { focusOpt(this, i); }
                        }
                        else { focusOpt(this, i); }
                    }
                }
            });
            this._prevChar[0] = C;
        },
        
        _uiHash: function () {
            var index = this.index();
            return {
                index: index,
                option: $("option", this.element).get(index),
                value: this.element[0].value
            };
        },
        
        open: function (event) {
            var self = this;
            var disabledStatus = this.newelement.attr("aria-disabled");
            if (disabledStatus != 'true') {
                this._refreshPosition();
                this._closeOthers(event);
                this.newelement
				.addClass('ui-state-active');
            	this.list
				.appendTo('body')
				.addClass(self.widgetFullName + '-open')
				.attr('aria-hidden', false);
            	if (this._selectedIndex() > 0) {
                    this.list.find('li:not(.' + self.widgetFullName + '-group):eq(' + this._selectedIndex() + ') a')[0].focus();
                }
                if (this.options.style == "dropdown") { this.newelement.removeClass('ui-corner-all').addClass('ui-corner-top'); }
                this._refreshPosition();
                this._trigger("open", event, this._uiHash());
                // Added to focus the chosen element always you open
                var activeID = this.widgetFullName + '-item-' + Math.round(Math.random() * 1000);
			    self._optionLis.eq(self._selectedIndex()).find('a:eq(0)').attr('id', activeID).focus();
            }
        },
        close: function (event, retainFocus) {
            if (this.newelement.is('.ui-state-active')) {
                this.newelement
				.removeClass('ui-state-active');
                this.list
				.attr('aria-hidden', true)
				.removeClass(this.widgetFullName + '-open');
                if (this.options.style == "dropdown") { this.newelement.removeClass('ui-corner-top').addClass('ui-corner-all'); }
                if (retainFocus) { this.newelement[0].focus(); }
                this._trigger("close", event, this._uiHash());
            }
        },
        change: function (event) {
            this.element.trigger('changeSelection');
            this._trigger("changeSelection", event, this._uiHash());
        },
        select: function (event) {
            this._trigger("select", event, this._uiHash());
        },
        _closeOthers: function (event) {
            $('.' + this.widgetFullName + '.ui-state-active').not(this.newelement).each(function () {
                $(this).data('selectelement').selectmenu('close', event);
            });
            $('.' + this.widgetFullName + '.ui-state-hover').trigger('mouseout');
        },
        _toggle: function (event, retainFocus) {
            if (this.list.is('.' + this.widgetFullName + '-open')) { this.close(event, retainFocus); }
            else { this.open(event); }
        },
        _formatText: function (text) {
            return this.options.format ? this.options.format(text) : text;
        },
        _selectedIndex: function () {
            return this.element[0].selectedIndex;
        },
        _selectedOptionLi: function () {
            // negative indexes are not supported
            if (this._selectedIndex() < 0)
                return $();

            return this._optionLis.eq(this._selectedIndex());
        },
        _focusedOptionLi: function () {
            return this.list.find('.' + this.widgetFullName + '-item-focus');
        },
        _moveSelection: function (amt) {
            var currIndex = parseInt(this._selectedOptionLi().data('index'), 10);
            var newIndex = currIndex + amt;
            return this._optionLis.eq(newIndex).trigger('mouseup');
        },
        _moveFocus: function (amt) {
            if (!isNaN(amt)) {
                var currIndex = parseInt(this._focusedOptionLi().data('index'), 10);
                var newIndex = currIndex + amt;
            }
            else { var newIndex = parseInt(this._optionLis.filter(amt).data('index'), 10); }

            if (newIndex < 0) { newIndex = 0; }
            if (newIndex > this._optionLis.size() - 1) {
                newIndex = this._optionLis.size() - 1;
            }
            var activeID = this.widgetFullName + '-item-' + Math.round(Math.random() * 1000);
    
            this._focusedOptionLi().find('a:eq(0)').attr('id', '');
            this._optionLis.eq(newIndex).find('a:eq(0)').attr('id', activeID)[0].focus();
            this.list.attr('aria-activedescendant', activeID);
        },
        
        
        _scrollPage: function (direction) {
        
            var numPerPage = Math.floor(this.list.outerHeight() / this.list.find('li:first').outerHeight());
            numPerPage = (direction == 'up') ? -numPerPage : numPerPage;
            this._moveFocus(numPerPage);
        },
        _setData: function (key, value) {
            this.options[key] = value;
            if (key == 'disabled') {
                this.close();
                this.element
				.add(this.newelement)
				.add(this.list)
					[value ? 'addClass' : 'removeClass'](
						this.widgetFullName + '-disabled' + ' ' +
						this.namespace + '-state-disabled')
					.attr("aria-disabled", value);
            }
        },
        _scrollPageInternal: function ()
        {  
        },
        index: function (newValue) {
            if (arguments.length) {
                this.element[0].selectedIndex = newValue;
                this._refreshValue();
            } else {
                return this._selectedIndex();
            }
        },

        value: function (newValue) {
            if (arguments.length) {
                this.element[0].value = newValue;
                this._refreshValue();
                this.select();
            } else {
                return this.element[0].value;
            }
        },

        _refreshValue: function () {
            var activeClass = (this.options.style == "popup") ? " ui-state-active" : "";
            var activeID = this.widgetFullName + '-item-' + Math.round(Math.random() * 1000);
            //deselect previous
            this.list
			.find('.' + this.widgetFullName + '-item-selected')
			.removeClass(this.widgetFullName + "-item-selected" + activeClass)
			.find('a')
			.attr('aria-selected', 'false')
			.attr('id', '');
            //select new
            this._selectedOptionLi()
			.addClass(this.widgetFullName + "-item-selected" + activeClass)
			.find('a')
			.attr('aria-selected', 'true')
			.attr('id', activeID);

            //toggle any class brought in from option
            var currentOptionClasses = this.newelement.data('optionClasses') ? this.newelement.data('optionClasses') : "";
            var newOptionClasses = this._selectedOptionLi().data('optionClasses') ? this._selectedOptionLi().data('optionClasses') : "";
            this.newelement
			.removeClass(currentOptionClasses)
			.data('optionClasses', newOptionClasses)
			.addClass(newOptionClasses)
			.find('.' + this.widgetFullName + '-status')
			.html(
				this._selectedOptionLi()
					.find('a:eq(0)')
					.html()
			);

            this.list.attr('aria-activedescendant', activeID)
        },
        _refreshPosition: function () {
            //set left value
            this.list.css('left', this.newelement.offset().left);

            //set top value
            var menuTop = this.newelement.offset().top;
            var scrolledAmt = this.list[0].scrollTop;
            this.list.find('li:lt(' + this._selectedIndex() + ')').each(function () {
                scrolledAmt -= $(this).outerHeight();
            });

            if (this.newelement.is('.' + this.widgetFullName + '-popup')) {
                menuTop += scrolledAmt;
                this.list.css('top', menuTop);
            }
            else {
                menuTop += this.newelement.height();
                this.list.css('top', menuTop);
            }
        }
    });
})(jQuery);